import gdb_mt_comet_hiking      from './db_mt_comet_hiking'
import gdb_mt_master 			from './db_mt_master';

// 2021-11-02, 하나의 산 정보를 만든다. 하이킹 인덱스 목록, 하이킹 건수, 전체 거리 등
// 등산기록: gdb_mt_comet_hiking
/* 
** output:
      objDMTData {   // 산 하나에 대한 Display용 자료
        // 구조체는 mt_hiking.vue 참조
      },
*/
function f_create_one_mt_data(sMT_ID, objDMTData) {
    // 표 만들기 테스트
    let i = 0;
    var iHikingNo         = 0;
    var iIndexOfHikings   = [];
    var iRepresentativeNo = 0;
    var iIdxOfRepresentative = []
    var dTotalDistance    = 0;
    var iTotalTime        = 0;      // 분단위

    // 하이킹 목록을 찾는다.
    for(i = 0; i < gdb_mt_comet_hiking.length; i++) {
        if(sMT_ID == gdb_mt_comet_hiking[i].MT_ID) {
            iIndexOfHikings[iHikingNo] = i;
            iHikingNo++;
            dTotalDistance += gdb_mt_comet_hiking[i].Distance;
            iTotalTime     += gdb_mt_comet_hiking[i].HikingTime;        // 산행시간, 분단위
            // 대표 하이킹인지 판단한다. 하이킹 수가 너무 많을 경우 대표 하이킹만 표시해준다.
            if(gdb_mt_comet_hiking[i].Representative == 'Y') {
                iIdxOfRepresentative[iRepresentativeNo] = i;
                iRepresentativeNo++;
            }
        }
    } // for i
    objDMTData.iHikingNo       = iHikingNo;
    objDMTData.iIndexOfHikings = iIndexOfHikings;
    objDMTData.dTotalDistance  = dTotalDistance;
    objDMTData.iTotalTime      = iTotalTime;

    console.log(sMT_ID + '  HikingNo: ' + iHikingNo);   /// ****

    // 화면에 무엇을 표시할지 결정한다.
    // 전제사항: 최소 하나 이상의 하이킹 레코드가 존재한다. 산 목록을 결정할 때, 미리 확인해야 함.
    if(iRepresentativeNo > 0 && iHikingNo > 3) { 
        objDMTData.iDisplayRowNo     = iRepresentativeNo;
        objDMTData.iDisplayHikingIdx = iIdxOfRepresentative;
        // 대표 산행 아래에 요약을 한 줄 넣어 준다.
        var iHikingTimeHour = parseInt(iTotalTime / 60);
        var iHikingTimeMinute = iTotalTime % 60;
        objDMTData.iDisplayRowSpan   = iRepresentativeNo + 1;
        objDMTData.sSummary    = '총 ' + iHikingNo + '회 산행, ' + dTotalDistance + 'km, ' 
                               + iHikingTimeHour + '시간 ' + iHikingTimeMinute + '분';
        objDMTData.iSummaryKey = iIdxOfRepresentative[0] + 1000;
        
    }
    else  { // if(iHikingNo > 0)
        objDMTData.iDisplayRowNo     = iHikingNo;
        objDMTData.iDisplayHikingIdx = iIndexOfHikings;
        objDMTData.iDisplayRowSpan   = iHikingNo;
        objDMTData.iSummaryKey       = -1;  // summary 사용하지 않음
    }
    

    return;
} // f_create_one_mt_data()

// 지역명에 해당하는 산 목록을 구한다.
// 예, 국립공원, 중부지방, 서울, 경기도
function f_get_mountains_of_the_region(sRegionName, objRegionInfo) {
    var aiMT_Idx = [];
    var i       = 0;
    var bNationalPark = 'Y';    // 국립공원을 고려한다.
    var asProvince = ['서울', '경기', '강원', '충북', '충남',
                      '전북', '전남', '경북', '경남', '제주'];

    if(sRegionName == '국립공원') {
        // 일단 국립공원에 포함된 모든 산을 구한다.
        for(i = 0; i < gdb_mt_master.length; i++) {
            if(gdb_mt_master[i].NationalPrak == 'Y') {
                aiMT_Idx.push(i);
            }
        }
        bNationalPark = 'N';    // 국립공원 검사하지 않도록 함
    } 
     else if(asProvince.includes(sRegionName)) {
        // 일단 국립공원에 포함된 모든 산을 구한다.
        for(i = 0; i < gdb_mt_master.length; i++) {
            if(gdb_mt_master[i].Province == sRegionName) {
                aiMT_Idx.push(i);
            }
        }
     }
	/* 2023-02-07. 임시로 작성한 코드 삭제함
     else if(sRegionName == '경기도') {
        objRegionInfo.sRegionName = sRegionName;
        objRegionInfo.arrsMT_ID = new Array('천마산', '용문산', '마니산');
     }
     else if(sRegionName == '중부지방') {
        objRegionInfo.sRegionName = sRegionName;
        objRegionInfo.arrsMT_ID = new Array('두타산', '삼악산', '오봉산');
     }
     else if(sRegionName == '남부지방') {
        objRegionInfo.sRegionName = sRegionName;
        objRegionInfo.arrsMT_ID = new Array('한라산', '성인봉', '구봉산_진안');
     }
	*/

    // 등산 이력이 있는 산만 결과값으로 복사한다.
    // *****????? 나중에 높이 기준 정렬한다.
    objRegionInfo.arrsMT_ID  = new Array();
    objRegionInfo.arriMT_Idx = new Array();
    for(i = 0; i < aiMT_Idx.length; i++) {
        if(gdb_mt_master[aiMT_Idx[i]].CometHiking == 'Y') {
            if(bNationalPark == 'Y' && gdb_mt_master[aiMT_Idx[i]].NationalPrak == 'Y')
                continue;   // 국립공원을 검사하는 경우. 이미 국립공원에 포함되어 있을 경우 목록에 추가할 필요 없음
            objRegionInfo.arrsMT_ID.push(gdb_mt_master[aiMT_Idx[i]].ID);
			// 2023-02-07, 산 인덱스를 추가했더니 동작을 안한다 --> new Array() 추가해서 해결
			objRegionInfo.arriMT_Idx.push(aiMT_Idx[i]);
        }
    } 
    // 산 목록이 하나라도 있을 경우만, 지역 이름을 복사한다.
    if(objRegionInfo.arrsMT_ID.length > 0)
        objRegionInfo.sRegionName = sRegionName;

    // *****????? 디버그용. 결과 값을 출력한다.
    console.log(sRegionName + ": " + objRegionInfo.arrsMT_ID);
 
} // gf_get_mountains_of_the_region()             '국립공원', '서울', '경기도', '중부지방', '남부지방'
 

// 2021-11-02, 한 페이지에 표시되는 자료를 모두 구성한다.
//     지역별로 나누고, 지역에 속하는 산 목록 및 하이킹 인덱스를 만든다.
// input:
//      asRegions: 지역 이름 목록. f_get_mountains_of_the_region() 함수에서 
//                 지역 이름을 보고 산 목록을 가져온다.
// output:
//     aobjRegionInfo: 이곳에 지역별 산과 하이킹 인덱스 값이 채워짐
function gf_create_region_info(asRegions, aobjRegionsInfo) {
        // 표 만들기 테스트
    let i = 0, k = 0;
    var iTotalHikingNo = 0;
    var dTotalDistance = 0;
 //   aobjRegionsInfo = new Array();      // ********?????????

    // 지역별 산 목록을 구한다. look k: 지역 하나
    for(k = 0; k < asRegions.length; k++) {
        var objRegionInfo = {};

        f_get_mountains_of_the_region(asRegions[k], objRegionInfo);
        console.log(objRegionInfo.arrsMT_ID);   /// ****

        objRegionInfo.arrobjDMTData = new Array();
        for(i = 0; i < objRegionInfo.arrsMT_ID.length; i++) {
            var objDMTData = {};
    
            f_create_one_mt_data(objRegionInfo.arrsMT_ID[i], objDMTData);

            console.log(objDMTData.iIndexOfHikings);
            
            // 하이킹이 하나라도 있을 경우
            if(objDMTData != null) {
                objRegionInfo.arrobjDMTData.push(objDMTData);
                iTotalHikingNo += objDMTData.iHikingNo;
                dTotalDistance += objDMTData.Distance;
            }
            ////////////// ****
            console.log('k: ' + k + 'i: ' + i);
            console.log("idxOfHikings:" + objRegionInfo.arrobjDMTData[i].iIndexOfHikings);
            console.log("hiking no:" + objRegionInfo.arrobjDMTData[i].iHikingNo);

        } // for i
        objRegionInfo.iTotalHikingNo  = iTotalHikingNo;
        objRegionInfo.dTotalDistance  = dTotalDistance;

        // 최종 리턴값
        aobjRegionsInfo.push(objRegionInfo);
    }
    return;
}

//export default gf_create_one_mt_data
export default gf_create_region_info
