import Vue from "vue"
import VueRouter from "vue-router"

import Home from "@/views/Home"
import GridSystem from "@/views/GridSystem"
import mt_hiking from "@/views/mt_hiking"

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'Home',
        component: Home
    },
    {
        path: '/grid-system',
        name: 'GridSystem',
        component: GridSystem
    },
    {
        path: '/mt-hiking',
        name: 'mt_hiking',
        component: mt_hiking
    },
]

const router = new VueRouter({
    mode: 'history',
    routes
})

export default router