<!-- *************************
  중요 구조체 설명
      // ***** Region Info: 국립공원, 중부지방, 남부지방, 해외 *****
      objRegionInfo: {
        sRegionName: '설악...',
        arrsMT_ID: [],  	// db table key
		arriMT_Idx: [],		// db table의 인덱스, 2023-02-07
        arrsDMT:   [],  	// display 용
        iMTNo:  2,
        iTotalHikingNo: 0,
        dTotalDistance: 0,
        arrobjDMTData: [],    // 구조체 아래 참조
      },
      // ****** 산 하나에 대한 Display용 자료 *******
          objDMTData {   
              iHikingNo: 0,
              iIndexOfHikings: [],
              iRepresentativeNo,        // *****????? 개발 필요
              iIdxOfRepresentative: [],
              iDisplayRowNo: 1,
              iDisplayHikingIdx: [],  // 하이킹 건수가 많을 경우, 대표 산행만 표시하고, 나머지는 '외 00 건으로 표시
              iDisplayRowSpan: 1,
              sSummary: '',
              iSummaryKey: 1,     // vue에서 키값을 요구한다.
              dTotalDistance: 0.0,
              iTotalTime: 0,
          },
-->

<template>
  <div>
    <!-- Region 목록에 대해 for문으로 반복 수행한다. 국립공원, 서울, 경기도?, 중부지방, 남부지방
         첫번째 루프: RInfo = Region Info
         두번째 루프: mt1   = region에 속하는 산 하나
         세번째 루프: hiking_idx = gdb_mt_comet_hiking[] 에서의 인덱스
    -->
    
    <div v-for="(RInfo, i) in aobjRegionsInfo" v-bind:key="i">
      <!------------ Region 제목 써주기 ----------------       {{RInfo.arrobjDMTData[i].iIndexOfHikings}}-->
      <h3>{{i}}: {{RInfo.sRegionName}}</h3>
      {{RInfo.arrsMT_ID}}
      <!------------ table 목록 시작 ------------------>
      <table class="mt_trip_table">
        <!-- template으로 Region에 속하는 산 목록에 대해 for문으로 반복 수행한다. -->
		<!-- 산 이름, 높이, 등산횟수를 적어 준다, 최근 등산 일자는 나중에 추가...-->
        <template v-for="(mt1, j) in RInfo.arrobjDMTData">
            <tr class="mt_trip_row" :key="mt1.iSummaryKey">
				<td class="mt_trip_col1">{{RInfo.arrsMT_ID[j]}}</td>
				<td class="mt_trip_col2"> {{gdb_mt_master[RInfo.arriMT_Idx[j]].Height_Meter}}  m</td>
				<td class="mt_trip_col_by100">
					<template v-if="gdb_mt_master[RInfo.arriMT_Idx[j]].Blackyak100 == 'Y'">	BY 100
					</template>
				</td>
				<td class="mt_trip_col3">{{mt1.iHikingNo}}</td>
            </tr>

		
        <!-- 산 하나에 대해 최대 3개의 행을 만들다. 
		**** 2023-02-07, 산 하나에 한줄만 만들도록 수정하면서 코멘트 처리*****
          <tr v-for="(hiking_idx, k) in mt1.iDisplayHikingIdx" :key="hiking_idx" 
              class="mt_trip_row">
            <td v-if="k === 0" :rowspan=mt1.iDisplayRowSpan
                class="mt_trip_col1"
            >
                  {{RInfo.arrsMT_ID[j]}}
            </td>
            <td class="mt_trip_col2">
              {{gdb_mt_comet_hiking[hiking_idx].StartDate.substring(0,4)}}년<br>
              {{gdb_mt_comet_hiking[hiking_idx].StartDate.substring(5,7)}}월
              {{gdb_mt_comet_hiking[hiking_idx].StartDate.substring(8,10)}}일
            </td>
            <td class="mt_trip_col3">
              {{gdb_mt_comet_hiking[hiking_idx].Path}}
              <br>
              {{gdb_mt_comet_hiking[hiking_idx].Distance}}km,
              {{gdb_mt_comet_hiking[hiking_idx].HikingTime2}},
            </td>
          </tr>
          <template v-if="mt1.iSummaryKey != -1">
            <tr class="mt_trip_row" :key="mt1.iSummaryKey"
            >
              <td class="mt_trip_col2"></td>
              <td class="mt_trip_col3">{{mt1.sSummary}}</td>
            </tr>
          </template>
           -->
		
		</template>
      </table>
    </div>
  </div>
</template>

<script>
  import gdb_mt_master          from '../database/db_mt_master'
  import gf_create_region_info  from '../database/gf_create_one_mt_data'
  import gdb_mt_comet_hiking    from '../database/db_mt_comet_hiking'
  
  export default {
    mounted: function() {
      // 지역 별 산 목록에 대해 하이킹 정보를 구한다.   '경기도', '중부지방', '남부지방'
      gf_create_region_info(this.asRegions, this.aobjRegionsInfo);
    },
    data () {
      return {
        aobjRegionsInfo: [],
        asRegions: [
          '국립공원', '서울', '경기', '강원', '충북', '충남', '전북', '전남',
          '경북', '경남', '제주'
        ],
        gdb_mt_master,
        gdb_mt_comet_hiking,
  }
    }
  }
</script>

<!-- 표현(스타일) -->
<style scoped>
  .mt_trip_table {
    display: table;
    width: 100%;
    border-collapse: collapse;
    border-color: rgba(170, 170, 170, .6)
  }

  .mt_trip_row {
    display: table-row;
    width: 100%;
    border-collapse: collapse;
    border-color: rgba(170, 170, 170, .6)
    }

  .mt_trip_col_by100 {
    display: table-cell;
    width: 90px;
    border: 1px solid #bcbcbc;
    border-collapse: collapse;
    text-align: center;
    padding: 4px 2px 4px 2px;
    }

  .mt_trip_col1 {
    display: table-cell;
    width: 90px;
    border: 1px solid #bcbcbc;
    border-collapse: collapse;
    text-align: center;
    padding: 4px 2px 4px 2px;
    }

  .mt_trip_col2 {
    display: table-cell;
    width: 90px;
    border: 1px solid #bcbcbc;
    border-collapse: collapse;
    text-align: right;
    padding: 4px 4px 4px 4px;
  }  
  .mt_trip_col3 {
    display: table-cell;
    border: 1px solid #bcbcbc;
    border-collapse: collapse;
    padding: 4px 4px 4px 4px;
  }  
</style>